/* eslint-disable quote-props */
/* eslint-disable react/prop-types */
import React from 'react';
import classNames from 'classnames';

const TodoItem = ({ todo, deleteTodo, markCompleted }) => {
  const completedClassToggle = classNames('', { 'completed': todo.completed });
  const ifChecked = todo.completed ? 'checked' : null;

  return (
    <li className={completedClassToggle}>
      <div className="view">
        <input
          type="checkbox"
          className="toggle"
          id={todo.id}
          onClick={markCompleted}
          defaultChecked={ifChecked}
        />
        <label htmlFor={todo.id}>{todo.text}</label>
        <button
          onClick={deleteTodo}
          type="button"
          className="destroy"
        />
      </div>
      <input type="text" className="edit" />
    </li>
  );
};

export default TodoItem;
